    padding-top: 7px;
    font-family: sans-serif;
    font-weight: 700;
    display: inline-block;
    color:white;
    width:40px;
    margin-left:6px;
    background-color:#5bc9b1;
}
.bar-sorted{
    background-color:#b979ec;
    
}
.bar-swap{
    background-color:#57a846;
}
.bar-min{
    background-color: red;
}
